import React from "react";
import Layout from '../../../components/Global/Layout/Layout'
import Header from '../../../components/Global/App/Header/Header.component'
import Footer from '../../../components/Global/App/Footer/Footer.component'
import ParallaxSectionOne from "../../../components/Local/Pages/Listing/Style/Parallax/Parallax.component";
import ParallaxSection2 from "../../../components/Local/Pages/Listing/Style/Parallax/Parallax2.component";

const Parallax = () => {
	return (
		<Layout>
			<Header />
			<ParallaxSectionOne/>
			<ParallaxSection2/>
			<Footer />
		</Layout>
	);
}

export default Parallax