import React,{useEffect} from 'react'
import { Row,Col,Nav,Tab } from 'react-bootstrap'
import AdvanceSearchOption from '../../../../../Global/App/AdvanceSearchOption/AdvanceSearchOption.component'

const ParallaxSectionOne=() =>{

  useEffect(() => {
        let currentPosition = window.scrollY;
        const parallax= document.getElementById("parallax")
        document.addEventListener("scroll" , ()=>{
        currentPosition = window.scrollY;
        parallax.style.backgroundPosition="50% "+ currentPosition/4 +"px"
      })
      
  })
    return (
        <section className="listing-home-bg parallax pt30-520" id="parallax" data-stellar-background-ratio="0.2">
		<div className="container">
			<div className="row posr">
				<div className="col-lg-12">
					<div className="home_content listing">
						<div className="home-text home6 text-center">
							<h2 className="fz50 color-white">Enjoy The Finest Homes</h2>
							<p className="fz18 color-white">From as low as $10 per day with limited time offer discounts.</p>
						</div>
                        <div className="home_adv_srch_opt listing">
                        <Tab.Container id="left-tabs-example" defaultActiveKey="buy">
                                    <Row>
                                        <Col sm={12}>
                                            <Nav id="pills-tab" className="nav nav-pills">
                                                <Nav.Item>
                                                    <Nav.Link eventKey="buy">Buy</Nav.Link>
                                                </Nav.Item>
                                                <Nav.Item>
                                                    <Nav.Link eventKey="rent">Rent</Nav.Link>
                                                </Nav.Item>
                                            </Nav>
                                        </Col>
                                        <Col sm={12}>
                                            <Tab.Content id="pills-tabContent" className="home1_adsrchfrm">
                                                <Tab.Pane eventKey="buy">
                                                    <AdvanceSearchOption ClassName="home6" style2 textthm3 index="1"/>
                                                </Tab.Pane>
                                                <Tab.Pane eventKey="rent">
                                                <AdvanceSearchOption  ClassName="home6" style2 textthm3 index="2"/>
                                                </Tab.Pane>
                                            </Tab.Content>
                                        </Col>
                                    </Row>
                                </Tab.Container>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default ParallaxSectionOne
