import React from 'react'
import ReactSelect from '../../../App/ReactSelect/ReactSelect'
import { Monthly } from '../../../App/ReactSelect/SelectData'

const MortageCalculator=()=>{

    return(
    <div className="sidebar_listing_list">
        <div className="sidebar_advanced_search_widget">
            <h4 className="title mb25">Mortgage Calculator</h4>
            <ul className="sasw_list mb0">
                <li className="search_area">
                    <div className="form-group">
                        <input type="text" className="form-control" id="exampleInputName1" placeholder="Total Amount"/>
                        <label htmlFor="exampleInputName1"><span className="flaticon-money-bag"></span></label>
                    </div>
                </li>
                <li className="search_area">
                    <div className="form-group">
                        <input type="text" className="form-control" id="exampleInputAmount" placeholder="Down Payment"/>
                        <label htmlFor="exampleInputAmount"><span className="flaticon-money-bag"></span></label>
                    </div>
                </li>
                <li className="search_area">
                    <div className="form-group">
                        <input type="text" className="form-control" id="exampleInputAmount2" placeholder="Interest Rate"/>
                        <label htmlFor="exampleInputAmount2"><span className="flaticon-percent"></span></label>
                    </div>
                </li>
                <li className="search_area">
                    <div className="form-group">
                        <input type="text" className="form-control" id="exampleInputYear" placeholder="Loan Term (Years)"/>
                        <label htmlFor="exampleInputYear"><span className="flaticon-calendar"></span></label>
                    </div>
                </li>
                <li>
                    <div className="search_option_two">
                        <div className="candidate_revew_select">
                        <ReactSelect data={Monthly} />
                        </div>
                    </div>
                </li>
                <li>
                    <div className="search_option_button style2">
                        <button type="submit" className="btn btn-block btn-thm">Search</button>
                    </div>
                </li>
            </ul>
        </div>
    </div>)
}
export default MortageCalculator