import React from 'react'
import {Link} from 'gatsby'
const CategoriesProperty=()=>{
    return(
    <div className="terms_condition_widget">
        <h4 className="title">Categories Property</h4>
        <div className="widget_list">
            <ul className="list_details">
                <li><Link to="/list/"><i className="fa fa-caret-right mr10"></i>Apartment <span className="float-right">6 properties</span></Link></li>
                <li><Link to="/list/"><i className="fa fa-caret-right mr10"></i>Condo <span className="float-right">12 properties</span></Link></li>
                <li><Link to="/list/"><i className="fa fa-caret-right mr10"></i>Family House <span className="float-right">8 properties</span></Link></li>
                <li><Link to="/list/"><i className="fa fa-caret-right mr10"></i>Modern Villa <span className="float-right">26 properties</span></Link></li>
                <li><Link to="/list/"><i className="fa fa-caret-right mr10"></i>Town House <span className="float-right">89 properties</span></Link></li>
            </ul>
        </div>
    </div>
    )
}
export default CategoriesProperty