import React from 'react'
import {graphql,useStaticQuery,Link} from 'gatsby'


const SidebarFeatured=({HeaderTitle})=>{
    const SidebarItem=({title,price,beds,baths,Sqft,children})=>{
    return(
        <div className="media">
                {children}
                <div className="media-body">
                    <h5 className="mt-0 post_title">{title}</h5>
                    <span>${price}/<small>/mo</small></span>
                    <ul className="mb0">
                        <li className="list-inline-item">Beds: {beds}</li>
                        <li className="list-inline-item">Baths: {baths}</li>
                        <li className="list-inline-item">Sq Ft: {Sqft}</li>
                    </ul>
                </div>
            </div>
    )
}
    const data=useStaticQuery(graphql`
    query{
        fls1: file(relativePath: {eq: "blog/fls1.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
        }
        fls2: file(relativePath: {eq: "blog/fls2.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
        }
        fls3: file(relativePath: {eq: "blog/fls3.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
        }


    }
    `)
    return(
        <div className="sidebar_feature_listing">
            <h4 className="title">{HeaderTitle}</h4>
            <Link to="/single/v1">
            <SidebarItem
                title="Nice Room With View"
                price="13000"
                beds='4'
                Sqft='5280'
                baths='2'
            >
                <img className="align-self-start mr-3" src={data.fls1.childImageSharp.fluid.originalImg} alt="fls1.jpg"/>
            </SidebarItem>
            </Link>
            <Link to="/single/v1">
            <SidebarItem
                title="Villa called Archangel"
                price="13000"
                beds='4'
                Sqft='5280'
                baths='2'

            >
                <img className="align-self-start mr-3" src={data.fls2.childImageSharp.fluid.originalImg} alt="fls1.jpg"/>
            </SidebarItem>
            </Link>
            <Link to="/single/v1">
            <SidebarItem
                title="Sunset Studio"
                price="13000"
                beds='4'
                Sqft='5280'
                baths='2'
            >
                <img className="align-self-start mr-3" src={data.fls3.childImageSharp.fluid.originalImg} alt="fls1.jpg"/>
            </SidebarItem>
            </Link>
            
        </div>
    )
}
export default SidebarFeatured